@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Roboto, sans-serif;
    font-style: normal;
}
.PromoPage {
    width: 100%;
    height: 100%;
}

.player {
    pointer-events: none;
}

.Banner {
    opacity: 0;
    z-index: -1;
    position: absolute;
    width: 176px;
    height: 387px;
    left: 1029px;
    top: 167px;
}

.active {
    opacity: 1;
    z-index: 100;
    transition: opacity 1.5s;
}

.bannerFrame {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    padding: 30px 10px 10px 10px;
    width: 251px;
    height: 357px;
    left: 0;
    top: 53px;
    background: #86D3F4;
}

.bannerHeader {
    font-weight: 500;
    font-size: 16px;
    text-transform: uppercase;
    color: #000000;
}

.qr-code {
    width: 126px;
    height: 126px;
}

.bannerText {
    width: 126px;
    font-weight: normal;
    font-size: 14px;
    color: #000000;
}

.bannerButton {
    width: 156px;
    height: 52px;
    background: #000000;
    border: none;
    font-weight: 500;
    font-size: 16px;
    text-transform: uppercase;
    color: #86D3F4;
    cursor: pointer;
}

.bannerButton:hover {
    background: #2a4362;
}
.ScreenKeyboardPage {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
}

.Panel {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    padding: 40px 24px;
    width: 380px;
    height: 100%;
    background: #86D3F4;
    border-top-left-radius: 3px;
}

.panelHeader {
    font-weight: normal;
    font-size: 26px;
    color: #000000;
}

.phone {
    font-weight: bold;
    font-size: 32px;
    text-transform: uppercase;
    color: #000000;
}

.phoneError {
    color: red;
}

.infoText {
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #000000;
    padding: 0 16px;
}

.ScreenKeyboard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px 0;
    width: 284px;
    height: 278px;
}

.KeyboardKey {
    min-width: 88px;
    padding: 12px 30px 12px 30px;
    border: 2px solid #000000;
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
    cursor: pointer;
}

.KeyboardKey:nth-of-type(10) {
    width: 186px;
}

.keyActive, .KeyboardKey:focus, .KeyboardKey:active, .KeyboardKey:hover{
    background: #000000;
    color: #FFFFFF;
    border: none;
}

.AgreeCheckbox {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0;
    width: 284px;
    height: 52px;
}

.inputWrapper {
    width: 40px;
    height: 40px;
    padding: 5px;
    border: 2px solid #000000;
}

.radio {
    -webkit-appearance: none;
    appearance: none;
    width: 100%;
    height: 100%;
}

.radio:checked {
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.labelRadio {
    font-weight: normal;
    font-size: 14px;
    color: #565656;
    text-align: left;
    margin-left: 20px;
    cursor: pointer;
}


.ButtonConfirmNumber {
    width: 284px;
    height: 52px;
    background: #000000;
    color: #FFFFFF;
    cursor: pointer;
    font-weight: 500;
    font-size: 16px;
    text-transform: uppercase;
}

.ButtonConfirmNumber:disabled {
    background: inherit;
    border: 1px solid #4E4E4E;
    color: #4E4E4E;
}

.ButtonConfirmNumber:focus {
    border: none;
}

.ImageInfoBlock {
    width: calc(100% - 380px);
    height: 100%;
    background-image: url(/static/media/boyAndDog.dea294f6.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.CancelButton {
    position: absolute;
    width: 88px;
    height: 52px;
    right: 1.56%;
    top: 2.78%;
    border: 2px solid #000000;
    background: #FFFFFF url(data:image/png;base64,) no-repeat center;
    background-size: cover;
    cursor: pointer;
}

.CancelButton:focus, .CancelButton:hover {
    border: none;
}

.qrCodeInfo {
    position: absolute;
    width: 314px;
    height: 110px;
    left: 926px;
    top: 570px;
}

.falseValid {
    font-weight: 500;
    font-size: 16px;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #EA0000;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  background: #530755;
}

.screenWrapper {
  width: 1306px;
  height: 887px;
  background-image: url(/static/media/smarttv.b42c3542.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: auto;
  margin-left: auto;
}

.screen {
  position: relative;
  width: 1280px;
  height: 721px;
  margin: 15px auto 0;
  background: #000000;
}
.SliderPage {
    position: relative;
    width: 100%;
    height: 100%;
}

.imageSlider {
    width: 100%;
    height: 100%;
}

.arrowsBlock {
    position: absolute;
    display: flex;
    width: 136px;
    height: 36px;
    left: 572px;
    top: 660px;
}

.button {
    width: 50%;
    height: 100%;
    border: none;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: inherit;
    cursor: pointer;
}

.button:focus {
    border: none;
}

.leftWhite {
    background-image: url(data:image/png;base64,);
}

.leftBlack {
    background-image: url(data:image/png;base64,);
}

.rightWhite {
    background-image: url(data:image/png;base64,);
}

.rightBlack {
    background-image: url(data:image/png;base64,);
}

.cancel {
    position: absolute;
    width: 88px;
    height: 52px;
    right: 1.56%;
    top: 2.78%;
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
}
